import React, {useEffect, useState} from 'react'

const Index = (props) => {
    const settingsDefault = {
        src: 'https://allods.mail.ru/media/originals/5fca168ceae6e.jpg',
        partsX: 5,
        partsY: 3,
        precision: 1,
        isFinished: false
    }
    const [content, setContent] = useState(null)
    const [settings, setSettings] = useState(settingsDefault)

    const Puzzle = {
        elements: [],
        setElements: function (config = settings) {
            for (let y = 0; y < config.partsY; y++) {
                this.elements[y] = []
                for (let x = 0; x < config.partsX; x++) {
                    // TODO: в большом варианте уходит за границы в первой загрузке
                    const randomPosition = (box) => {
                        if (box) {
                            const minMax = {
                                top: {
                                    min: -y * box.getBoundingClientRect().height / config.partsY,
                                    max: box.getBoundingClientRect().height - (y + 1) * box.getBoundingClientRect().height / config.partsY
                                },
                                left: {
                                    min: -x * box.getBoundingClientRect().width / config.partsX,
                                    max: box.getBoundingClientRect().width - (x + 1) * box.getBoundingClientRect().width / config.partsX
                                }
                            }
                            const top = Math.round(minMax.top.min + Math.random() * (minMax.top.max - minMax.top.min)) + 'px'
                            const left = Math.round(minMax.left.min + Math.random() * (minMax.left.max - minMax.left.min)) + 'px'
                            return {top: top, left: left}
                        } else {
                            const minMax = {
                                top: {
                                    min: -y * 100 / config.partsY,
                                    max: 100 - (y + 1) * 100 / config.partsY
                                },
                                left: {
                                    min: -x * 100 / config.partsX,
                                    max: 100 - (x + 1) * 100 / config.partsX
                                }
                            }
                            const top = Math.round(minMax.top.min + Math.random() * (minMax.top.max - minMax.top.min)) + '%'
                            const left = Math.round(minMax.left.min + Math.random() * (minMax.left.max - minMax.left.min)) + '%'
                            return {top: top, left: left}
                        }
                    }
                    const style = {
                        position: 'absolute',
                        display: 'block',
                        top: randomPosition().top,
                        left: randomPosition().left,
                        maxHeight: '100%',
                        maxWidth: '100%',
                        zIndex: 0,
                        clipPath: `polygon(${x * 100 / config.partsX}% ${y * 100 / config.partsY}%, ${(x + 1) * 100 / config.partsX}% ${y * 100 / config.partsY}%, ${(x + 1) * 100 / config.partsX}% ${(y + 1) * 100 / config.partsY}%, ${x * 100 / config.partsX}% ${(y + 1) * 100 / config.partsY}%)`,
                        transition: '.5s',
                        cursor: 'pointer'
                    }
                    this.elements[y] = [...this.elements[y],
                        {
                            status: false,
                            element:
                                <img
                                    key={`${y}/${x}`}
                                    src={settings.src}
                                    alt={`Content error ${y}/${x}`}
                                    draggable={false}
                                    style={style}
                                    onDragStart={e => { e.preventDefault() }}
                                    onLoad={ev => {
                                        const item = ev.currentTarget
                                        const box = item.parentElement
                                        item.addEventListener('contextmenu', (e) => {e.preventDefault()})
                                        const position = randomPosition(item)
                                        item.style.top = position.top
                                        item.style.left = position.left


                                        const currentPosition = (event) => {
                                            const offset = {
                                                x: (x + .5) * item.getBoundingClientRect().width/config.partsX,
                                                y: (y + .5) * item.getBoundingClientRect().height/config.partsY
                                            }
                                            const page = {
                                                x: event.pageX===undefined ? event.changedTouches[0].pageX : event.pageX,
                                                y: event.pageY===undefined ? event.changedTouches[0].pageY : event.pageY
                                            }
                                            return {
                                                x: page.x - box.getBoundingClientRect().left - offset.x,
                                                y: page.y - box.getBoundingClientRect().top - offset.y,
                                            }
                                        }
                                        const setPosition = (position) => {
                                            item.style.left = typeof position.x == "string" ? position.x : position.x+'px'
                                            item.style.top = typeof position.y == "string" ? position.y : position.y+'px'
                                        }
                                        const currentPrecision = {
                                            get: function (position) {
                                                return Math.sqrt(position.x*position.x + position.y*position.y)
                                            },
                                            result: function (position) {
                                                const center = {
                                                    x: (box.getBoundingClientRect().width - item.getBoundingClientRect().width)*.5,
                                                    y: (box.getBoundingClientRect().height - item.getBoundingClientRect().height)*.5
                                                }
                                                const minSide = .5 * (item.getBoundingClientRect().width < item.getBoundingClientRect().height
                                                    ? item.getBoundingClientRect().width/config.partsX
                                                    : item.getBoundingClientRect().height/config.partsY)
                                                const mainPrecision = config.precision * this.get({x:minSide, y:minSide})
                                                const itemPrecision = this.get({x:position.x-center.x, y:position.y-center.y})
                                                return mainPrecision >= itemPrecision;
                                            }
                                        }

                                        const onMouseDown = (event) => {
                                            item.removeEventListener('mousedown', onMouseDown)
                                            document.addEventListener('mousemove', onMouseMove)
                                            document.addEventListener('mouseup', onMouseUp)
                                            item.removeEventListener('touchstart', onMouseDown)
                                            document.addEventListener('touchmove', onMouseMove)
                                            document.addEventListener('touchend', onMouseUp)

                                            const position = currentPosition(event)
                                            setPosition(position)
                                            this.updateElementStatus([y,x], false)

                                            item.style.opacity = '.7'
                                            item.style.cursor = 'move'
                                            item.style.zIndex++
                                        }
                                        const onMouseMove = (event) => {
                                            item.style.transition = '0s'
                                            const position = currentPosition(event)
                                            setPosition(position)
                                        }
                                        const onMouseUp = (event) => {
                                            item.addEventListener('mousedown', onMouseDown)
                                            document.removeEventListener('mousemove', onMouseMove)
                                            document.removeEventListener('mouseup', onMouseUp)
                                            item.addEventListener('touchstart', onMouseDown)
                                            document.removeEventListener('touchmove', onMouseMove)
                                            document.removeEventListener('touchend', onMouseUp)

                                            const position = currentPosition(event)
                                            const precision = currentPrecision.result(position)
                                            if (precision) {
                                                const center = {
                                                    x: (box.getBoundingClientRect().width - item.getBoundingClientRect().width)*.5,
                                                    y: (box.getBoundingClientRect().height - item.getBoundingClientRect().height)*.5
                                                }
                                                setPosition({x: center.x, y: center.y})
                                                this.updateElementStatus([y,x], true)
                                            } else {
                                                setPosition(position)
                                            }

                                            item.style.transition = style.transition
                                            item.style.opacity = '1'
                                            item.style.cursor = style.cursor
                                            item.style.zIndex--
                                        }
                                        item.addEventListener('touchstart', onMouseDown)
                                        item.addEventListener('mousedown', onMouseDown)
                                    }}
                                />
                        }
                    ]
                }
            }
        },
        get: function () {
            return this.elements.map(e => e.map(el => el.element))
        },
        updateElementStatus: function (pos, value) {
            this.elements[pos[0]][pos[1]] = {
                ...this.elements[pos[0]][pos[1]],
                status: value
            }
            let result = true
            for (let y = 0; y < this.elements.length; y++) {
                for (let x = 0; x < this.elements[y].length; x++) {
                    if (this.elements[y][x].status===false) { result = false }
                }
            }
            if (result) {
               this.setPassed()
            }
        },
        setPassed: function () {
            const element = <img
                draggable={false}
                src={settings.src}
                alt={'Content error'}
                style={{
                    maxHeight: '100%',
                    maxWidth: '100%',
                    padding: '2rem'
                }}
            />
            setContent(element)
            settings.callback && settings.callback()
        }
    }

    useEffect(() => {
        setSettings({...settings, ...props.settings})
    }, [props])
    useEffect(() => {
        if (settings.isFinished) {
            Puzzle.setPassed()
        } else {
            Puzzle.setElements(settings)
            setContent(Puzzle.get())
        }
    }, [settings])

    return (
        <div
            style={{
                flex:'1 1 100%',
                position:'relative',
                height:'100%',
                maxHeight:'inherit',
                width:'inherit',
                maxWidth:'inherit',
                overflow: 'hidden',
                display: 'flex',
                flexFlow: 'column nowrap',
                justifyContent: 'center',
                alignItems: 'center'
            }}
            draggable={false}
            onMouseDown={e => e.preventDefault()}
            onDoubleClick={e => e.preventDefault()}
            onContextMenu={e => e.preventDefault()}
            onDragStart={e => e.preventDefault()}
            onDrag={e => e.preventDefault()}
            onTouchStart={e => e.preventDefault()}
            onTouchMove={e => e.preventDefault()}
        >
            {content}
        </div>
    )
}

export default Index