import React from "react"
import {BrowserRouter, Route, Switch} from "react-router-dom";
import config from "../../config";
import Index from "./pages/index/indexContainer"
// import About from "./pages/about/indexContainer"
import Event from "./pages/events/indexContainer"

import Puzzle from './modules/modules/puzzle/index'

const Body = (props) => {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path={`${config.repo}/puzzle`} render={() => <Puzzle/>}/>

                <Route exact path={`${config.repo}/about`} render={() => <Index/>}/>
                <Route strict path={`${config.repo}/events`} render={() => <Event/>}/>
                <Route strict path={[config.repo,'/']} render={() => <Index/>}/>
            </Switch>
        </BrowserRouter>
    )
}

export default Body