import React from "react";
import config from "../../../../config";
import Index from './index'
import {BrowserRouter, Route, Switch} from "react-router-dom";

const IndexContainer = (props) => {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path={`${config.repo}/events/:year/:name`} render={(props) => <Index year={props.match.params.year} folder={props.match.params.name}/>}/>
                <Route strict path={[`${config.repo}/events`]} render={() => <Index/>}/>
            </Switch>
        </BrowserRouter>
    )
}

export default IndexContainer