import config from "../../../../config";
import style from './index.module.css'
import eventsList from '../../modules/events/eventList'
import {NavLink} from "react-router-dom";

const Index = (props) => {
    return (
        <main>
            <div className={style.block}>
                <h1>Ивенты</h1>

                <NavLink to={`${config.repo}/puzzle`}>Puzzle</NavLink>

                <div className={style.carousel}>
                    {eventsList.map(eventName =>
                        <NavLink to={eventName.href ? `${config.repo}${eventName.href}` : `${config.repo}/`} key={eventName.name}>
                        <div className={style.element} style={eventName.style && eventName.style}>
                            <h2>{eventName.name}</h2>
                            <p>{eventName.desc}</p>
                        </div>
                        </NavLink>
                    )}
                </div>
            </div>
        </main>
    )
}

export default Index